
.uploadLayer{
    position: relative;
    float: left;
    width:120px;
    height:120px;
}
.uploadLayer .uploadimg{
    width: 120px;
    height: 120px;
    border-radius: 100%;
    position: absolute;
}
.uploadLayer .layui-box{
    position: relative;
    top: 0;
    padding-top:35%;
    left: 0;
    width: 100%;
    height:100%;
    cursor: pointer;
    background-color: rgba(0,0,0,0);
    color: rgba(255,255,255,1);
    border:0;
}



